import { Alert, Card, CardContent, Grid, styled } from "@mui/material";
import DOMPurify from "dompurify";
import Markdown from "marked-react";

import { useTask } from "../../../../Application";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import Form from "./Form";

const MarkdownContainer = styled("div")(({ theme }) => ({
	"& h3": {
		marginTop: 0,
	},
}));

const Page = () => {
	const GUIDE = `
### نکات آموزشی

هر زمان قصد داشتید به مشتری اعلام کنید که سفارشش آماده شده و زمان تحویل گرفتن آن فرا رسیده است؛ از این افزونه استفاده کنید.

نمونه‌هایی از کاربرد افزونه اطلاع‌رسانی:
در خشکشویی لباس یک مشتری آماده تحویل است.
در مغازه تعمیرات موبایل، تعمیر یک گوشی به اتمام رسیده و آماده تحویل است.
خانمی لباس دلخواهش را به مزون سفارش داده و آماده تحویل است.
در آتلیه، عکس‌ها و آلبوم فرمالیته یک زوج به صورت نهایی تکمیل شده و آماده تحویل است.
مغازه‌های لوازم کودک میتوانند بعد از 1 سال به مشتریانی که برای نوزادان خود سیسمونی خرید کرده‌اند ملزومات مورد نیاز برای کودک یک ساله را اطلاع دهند.
و …
چهار عنوان و متن پیامک در این بخش قابل تنظیم است.
عنوان هایی که در این بخش وارد می‌کنید بر روی نمایشگر دستگاه دایرکت قابل مشاهده است و از روی صفحه کلید دستگاه می‌توان یکی از آنها را انتخاب کرد.
بعد از انتخاب یکی از عنوان‌ها از روی صفحه کلید پیامک مربوط به همان عنوان برای مشتری ارسال میشود.

برای استفاده از افزونه اطلاع رسانی بعد از تهیه شدن سفارش مشتری، دکمه‌ی تیک دستگاه را به مدت ۵ ثانیه نگه دارید.
سپس شماره مشتری را وارد کنید و از بین گزینه ها، پیامک اطلاع رسانی مورد نظر خود را انتخاب کنید تا پیامک آن برای مشتری ارسال شود.

پیام‌های حاوی محتوای نامناسب و فیشینگ ارسال نخواهد شد و در صورت مشاهده پنل کاربری مسدود خواهد شد.

`;

	const task = useTask("Customers-Club.notification");

	return (
		<TabView {...task}>
			<ResponsiveContainer>
				<Card variant="outlined" sx={{ mb: 3 }}>
					<CardContent sx={{ p: 3 }}>
						<Grid container spacing={5}>
							<Grid item xs={12} sm={6}>
								<Alert color="info">
									<MarkdownContainer>
										<Markdown breaks={true}>
											{DOMPurify.sanitize(GUIDE)}
										</Markdown>
									</MarkdownContainer>
								</Alert>
							</Grid>
							<Grid item xs={12} md={6}>
								<Form />
							</Grid>
						</Grid>
					</CardContent>
				</Card>
			</ResponsiveContainer>
		</TabView>
	);
};

export default Page;
