import {
	Box,
	Button,
	ListItem,
	ListItemIcon,
	ListItemText,
} from "@mui/material";
import { useSnackbar } from "notistack";
import { useTranslation } from "react-i18next";
import { useMiddleware } from "../../../../Application";
import QuantityInput from "../../../../components/QuantityInput";
import TextField from "../../../../components/TextField";
import useForm from "../../../../utils/useForm";
import SmsField from "../../components/SmsField";

const Form = () => {
	const { t } = useTranslation(["customers-club", "global"]);

	const { enqueueSnackbar } = useSnackbar();

	const middleware = useMiddleware("Customers-Club.Loyalties");

	const { register, setValue, getValue, loading, handleSubmit } = useForm({
		initialValues: {
			content: "",
			active: "",
			count: 1,
			interval: "",
			options: {
				delayHour: 1,
			},
		},
		handlePost: (data) => middleware.createOrUpdate(data),
		onSubmit: (response) => {
			enqueueSnackbar(
				t("global:general.update_successful", "Update successful")
			);
			// setInitialValues(response.options);
		},
	});

	return (
		<Box>
			<Box sx={{ mb: 2 }}>
				<TextField
					InputLabelProps={{ shrink: true }}
					fullWidth={true}
					type="text"
					label={t("عنوان")}
					sx={{ mb: 2 }}
					{...register("name")}
				/>

				<SmsField placeholder={t("متن پیام...")} />

				<Box sx={{ my: 2 }} />

				<ListItem disableGutters disablePadding>
					<ListItemText
						primary={t("تعداد برنده ها")}
						secondary={t(
							"در هر دوره به چه تعداد مشتری پیامک ارسال گردد؟"
						)}
					/>
					<ListItemIcon>
						<QuantityInput
							step={1}
							value={getValue("options.delayHour")}
							onChange={(e, value) =>
								setValue("options.delayHour", value)
							}
						/>
					</ListItemIcon>
				</ListItem>
			</Box>

			<Box sx={{ my: 3 }} />

			<Button
				variant="contained"
				onClick={handleSubmit}
				disabled={loading}
			>
				{t("global:general.save_changes")}
			</Button>
		</Box>
	);
};

export default Form;
